.profile-container {
    width: 20rem;
    margin: 2rem auto;
    padding: 1rem;
    border: 1px solid var(--red);
    background: var(--themecolor1);
    box-shadow: 0 0 8px var(--red);
    animation: pop 0.4s ease-out 0s 1 normal none;
}
.profile-avatar {
    width: 12rem;
    height: 12rem;
    margin: auto;
    position: relative;
}
.profile-avatar img {
    width: 100%;
    height: 100%;
    border-radius: 200px;
    margin: auto;
    background: var(--themecolor2);
    box-shadow: 0 0 16px black;
}
.profile-username {
    width: 100%;
    text-align: center;
    color: var(--color1);
    padding: 1rem;
    font-size: 1.4rem;
}
.profile-change {
    position: absolute;
    bottom: 0;
    left: 0; 
    right: 0; 
    margin-left: auto; 
    margin-right: auto; 
    width: 60%;
    text-align: center;
    padding: 8px;
    background: var(--themecolor2);
    box-shadow: 0 0 5px black;
    border: none;
    color: white;
    font-size: 0.8rem;
    transition: visibility 0.4s, opacity 0.4s, background-color 0.4s linear;
    visibility: visible;
    opacity: 1;
    cursor: pointer;
    border-radius: 4px;
}
.profile-change:hover {
    background: var(--red);
}
.profile-avatar-change {
    display: none;
}
.profile-change-hidden {
    visibility: hidden;
    opacity: 0;
    transition: visibility 0.4s, opacity 0.4s linear;
}
.profile-verification {
    width: 80%;
    text-align: center;
    color: var(--red);
    padding: 0.5rem;
    font-size: 1rem;
    background: var(--themecolor2);
    box-shadow: 0 0 5px black;
    margin: 1rem auto 0 auto;
    border-radius: 24px;
}
.verified {
    color: rgb(30, 240, 142);
}