@import url(https://fonts.googleapis.com/css2?family=Roboto&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Noto+Sans+SC&display=swap);
* {
  font-family: 'Noto Sans SC', Roboto;
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}
a {
  text-decoration: none;
  color: var(--color1);
}
a:visited {
  color: var(--color1);
}
#root {
  height: 100vh;
  overflow: hidden;
}
.App {
  height: 100vh;
}

* {
    --themecolor1: rgb(1, 10, 16);
    --themecolor2: rgb(13, 19, 23);
    --color1: rgb(255, 255, 255);
    --red: rgb(165, 1, 19);
}
.header {
    background: var(--themecolor1);
    color: var(--color1);
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0 1rem;
    box-shadow: 0px 1px 5px black;
    height: 5rem;
}
.title {
    font-size: 1.4rem;
}
.nav-bar {
    display: flex;
}
.nav-bar div {
    padding: 8px 16px;
}
.template {
    background: var(--themecolor2);
    height: 100%;
}
.header-img {
    max-height: 60%;
    margin-right: 1rem;
    -webkit-animation: spin 4s linear 0s infinite normal none;
            animation: spin 4s linear 0s infinite normal none;
}
.nav {
    display: flex;
    height: 100%;
    align-items: center;
}
.nav-active {
    color: var(--red)
}
@-webkit-keyframes spin {
    0% {transform: rotate(0deg);}
    100% {transform: rotate(360deg);}
}
@keyframes spin {
    0% {transform: rotate(0deg);}
    100% {transform: rotate(360deg);}
}
.selecting {
    color: var(--red)
}
.nav-username {
    margin-left: 1rem;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    cursor: pointer;
}
.nav-profile {
    padding: 0.5rem 1rem;
    border-radius: 50px;
    border: 2px solid var(--red);
    background: var(--themecolor2);
    display: flex;
    height: 3.5rem;
    justify-content: space-between;
    align-items: center;
    box-shadow: 0 0 8px var(--red);
    cursor: pointer;
}
.nav-avatar {
    height: 2.5rem;
    width: 2.5rem;
    border-radius: 1.25rem;
}
.profile-popup {
    position: absolute;
    box-shadow: 0 0 8px black;
    background: var(--themecolor2);
    border-radius: 5px;
    top: 4.5rem;
    right: 1rem;
    padding: 0.5rem;
    width: 8rem;
}
.popup-hidden {
    display: none;
}
.popup-button {
    padding: 0.5rem;
    background: transparent;
    border: none;
    border-bottom: 1px solid var(--red);
    color: var(--color1);
    font-size: 1rem;
    width: 100%;
    cursor: pointer;
    transition-duration: 0.5s;
}
.popup-button:hover {
    background: var(--red);
}
.auth-form {
    width: 20rem;
    margin: 1rem auto 0 auto;
    padding: 8px 16px 16px;
    background: var(--themecolor1);
    color: white;
    box-shadow: 0 0 8px rgb(15,25,35);
    -webkit-animation: pop 0.4s ease-out 0s 1 normal none;
            animation: pop 0.4s ease-out 0s 1 normal none;
    position: relative;
}
@-webkit-keyframes pop {
    0% {transform: translateY(100vh); opacity: 0;}
    100% {transform: translateY(0vh); opacity: 1}
}
@keyframes pop {
    0% {transform: translateY(100vh); opacity: 0;}
    100% {transform: translateY(0vh); opacity: 1}
}
.form-authen {
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    background: rgba(0,0,0,0.5);
}
.form-authen img {
    max-width: 7rem;
    position: absolute;
    margin: auto;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    -webkit-animation: spin 0.5s linear 0s infinite normal none;
            animation: spin 0.5s linear 0s infinite normal none;
}
.signup-form {
    width: 40rem;
}
.auth-form h2 {
    padding-bottom: 8px;
    border-bottom: 1px solid var(--red)
}
.form-content {
    display: flex;
    justify-content: space-evenly;
    align-items: flex-start;
}
.form-left {
    padding-right: 16px;
    border-right: 1px solid var(--themecolor2);
    width: 50%;
}
.form-right {
    padding-left: 16px;
    width: 50%;
}
.radio-group {
    height: 2.5rem;
    background: white;
    display: flex;
}
.form-radio {
    width: 50%;
    border: none;
    border-radius: 0;
    background: var(--themecolor2);
    color: var(--color1);
    font-size: 1rem;
    transition-duration: 0.5s;
}
.radio-active {
    background: var(--red);
}
.form-submit-button {
    width: 100%;
    margin-top: 1rem;
    padding: 8px;
    background: var(--red);
    border: none;
    color: var(--color1);
    font-size: 1.2rem;
    
}
.auth-form button:focus {
    outline: none
}
.auth-form input {
    height: 2.5rem;
    padding: 0 5px;
    font-size: 1rem;
}

.auth-form-group {
    margin-top: 4px;
}
.form-control {
    display: block;
    width: 100%;
    padding: 4px 0;
}
.form-control:focus {
    outline: none
}
.form-error {
    color: rgb(201, 3, 3);
    font-style: italic;
    font-size: 0.75rem;
    height: 1rem;
}
.form-switch {
    text-align: right;
    margin-top: 0.5rem;
    text-decoration: underline;
    font-style: italic;
    font-size: 0.8rem;
    cursor: pointer;
}
.home {
    color: var(--color1);
    -webkit-animation: pop 0.4s ease-out 0s 1 normal none;
            animation: pop 0.4s ease-out 0s 1 normal none;
}
.home h1 {
    text-align: center;
    padding: 2rem;
}
.home h2 {
    text-align: center;
}
.home-nav-container {
    width: 20rem;
    margin: 2rem auto;
    background: var(--themecolor1);
    box-shadow: 0px 1px 5px black;
    padding: 1rem 1rem 0rem 1rem;
}
.home-nav-button {
    padding: 16px;
    background: var(--red);
    border: none;
    color: var(--color1);
    font-size: 1.2rem;
    width: 100%;
    margin-bottom: 1rem;
}
.signoff-container {
    -webkit-animation: pop 0.4s ease-out 0s 1 normal none;
            animation: pop 0.4s ease-out 0s 1 normal none;
}
.admin-container {
    -webkit-animation: pop 0.4s ease-out 0s 1 normal none;
            animation: pop 0.4s ease-out 0s 1 normal none;
}
.profile-container {
    width: 20rem;
    margin: 2rem auto;
    padding: 1rem;
    border: 1px solid var(--red);
    background: var(--themecolor1);
    box-shadow: 0 0 8px var(--red);
    -webkit-animation: pop 0.4s ease-out 0s 1 normal none;
            animation: pop 0.4s ease-out 0s 1 normal none;
}
.profile-avatar {
    width: 12rem;
    height: 12rem;
    margin: auto;
    position: relative;
}
.profile-avatar img {
    width: 100%;
    height: 100%;
    border-radius: 200px;
    margin: auto;
    background: var(--themecolor2);
    box-shadow: 0 0 16px black;
}
.profile-username {
    width: 100%;
    text-align: center;
    color: var(--color1);
    padding: 1rem;
    font-size: 1.4rem;
}
.profile-change {
    position: absolute;
    bottom: 0;
    left: 0; 
    right: 0; 
    margin-left: auto; 
    margin-right: auto; 
    width: 60%;
    text-align: center;
    padding: 8px;
    background: var(--themecolor2);
    box-shadow: 0 0 5px black;
    border: none;
    color: white;
    font-size: 0.8rem;
    transition: visibility 0.4s, opacity 0.4s, background-color 0.4s linear;
    visibility: visible;
    opacity: 1;
    cursor: pointer;
    border-radius: 4px;
}
.profile-change:hover {
    background: var(--red);
}
.profile-avatar-change {
    display: none;
}
.profile-change-hidden {
    visibility: hidden;
    opacity: 0;
    transition: visibility 0.4s, opacity 0.4s linear;
}
.profile-verification {
    width: 80%;
    text-align: center;
    color: var(--red);
    padding: 0.5rem;
    font-size: 1rem;
    background: var(--themecolor2);
    box-shadow: 0 0 5px black;
    margin: 1rem auto 0 auto;
    border-radius: 24px;
}
.verified {
    color: rgb(30, 240, 142);
}
.dev-container {
    width: 40rem;
    margin: 2px auto 0 auto;
    -webkit-animation: pop 0.4s ease-out 0s 1 normal none;
            animation: pop 0.4s ease-out 0s 1 normal none;
}
.dev-container h1 {
    color: White;
    padding: 0.5rem;
    text-align: center;
    border-bottom: 1px solid var(--themecolor1);
    margin-bottom: 1rem;
}
.dev-container form {
    display: flex;
    flex-direction: column;
    padding: 1rem;
    background: var(--themecolor1);
    box-shadow: 0 0 5px rgb(33,33,33);
}
.dev-container form label {
    color: White;
    margin-bottom: 5px;
}
.dev-container form input {
    padding: 0.4rem 0.5rem;
    background: var(--color1);
    border: none;
    color: black;
}
.log-container {
    margin-top: 1rem;
    background: var(--themecolor1);
    box-shadow: 0 0 5px rgb(33,33,33);
    color: white;
    max-height: 20rem;
    overflow-y: overlay;
    scrollbar-width: 0;
}
.log-container h2 {
    text-align: center;
    padding: 1rem;
}
.log-container::-webkit-scrollbar { /* WebKit */
    width: 8px;
}
.log-item {
    padding: 0.5rem 1rem;
    display: flex;
    justify-content: space-between;
    align-items: center;
    height: 2.5rem;
}
::-webkit-scrollbar-thumb {
    background: var(--red);
}
.log-item:nth-child(even) {
    background: var(--themecolor2);
}
.log-delete {
    padding: 0.1rem 0.5rem;
    border: none;
    background: var(--red);
    color: white;
    transition: visibility 0.3s, opacity 0.3s, background-color 0.3s ease-in;
    visibility: hidden;
    opacity: 0;
}
.log-visible {
    visibility: visible;
    opacity: 1;
    transition: visibility 0.3s, opacity 0.3s ease-in;
}
.log-loader {
    height: 5rem;
    display: flex;
    justify-content: space-around;
    padding: 0 8rem;
    align-items: center;
}
.log-loader img {
    max-height: 70%;
    -webkit-animation: spin 1s linear 0s infinite normal none;
            animation: spin 1s linear 0s infinite normal none;
}
