.home {
    color: var(--color1);
    animation: pop 0.4s ease-out 0s 1 normal none;
}
.home h1 {
    text-align: center;
    padding: 2rem;
}
.home h2 {
    text-align: center;
}
.home-nav-container {
    width: 20rem;
    margin: 2rem auto;
    background: var(--themecolor1);
    box-shadow: 0px 1px 5px black;
    padding: 1rem 1rem 0rem 1rem;
}
.home-nav-button {
    padding: 16px;
    background: var(--red);
    border: none;
    color: var(--color1);
    font-size: 1.2rem;
    width: 100%;
    margin-bottom: 1rem;
}