* {
    --themecolor1: rgb(1, 10, 16);
    --themecolor2: rgb(13, 19, 23);
    --color1: rgb(255, 255, 255);
    --red: rgb(165, 1, 19);
}
.header {
    background: var(--themecolor1);
    color: var(--color1);
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0 1rem;
    box-shadow: 0px 1px 5px black;
    height: 5rem;
}
.title {
    font-size: 1.4rem;
}
.nav-bar {
    display: flex;
}
.nav-bar div {
    padding: 8px 16px;
}
.template {
    background: var(--themecolor2);
    height: 100%;
}
.header-img {
    max-height: 60%;
    margin-right: 1rem;
    animation: spin 4s linear 0s infinite normal none;
}
.nav {
    display: flex;
    height: 100%;
    align-items: center;
}
.nav-active {
    color: var(--red)
}
@keyframes spin {
    0% {transform: rotate(0deg);}
    100% {transform: rotate(360deg);}
}
.selecting {
    color: var(--red)
}
.nav-username {
    margin-left: 1rem;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    cursor: pointer;
}
.nav-profile {
    padding: 0.5rem 1rem;
    border-radius: 50px;
    border: 2px solid var(--red);
    background: var(--themecolor2);
    display: flex;
    height: 3.5rem;
    justify-content: space-between;
    align-items: center;
    box-shadow: 0 0 8px var(--red);
    cursor: pointer;
}
.nav-avatar {
    height: 2.5rem;
    width: 2.5rem;
    border-radius: 1.25rem;
}
.profile-popup {
    position: absolute;
    box-shadow: 0 0 8px black;
    background: var(--themecolor2);
    border-radius: 5px;
    top: 4.5rem;
    right: 1rem;
    padding: 0.5rem;
    width: 8rem;
}
.popup-hidden {
    display: none;
}
.popup-button {
    padding: 0.5rem;
    background: transparent;
    border: none;
    border-bottom: 1px solid var(--red);
    color: var(--color1);
    font-size: 1rem;
    width: 100%;
    cursor: pointer;
    transition-duration: 0.5s;
}
.popup-button:hover {
    background: var(--red);
}