.dev-container {
    width: 40rem;
    margin: 2px auto 0 auto;
    animation: pop 0.4s ease-out 0s 1 normal none;
}
.dev-container h1 {
    color: White;
    padding: 0.5rem;
    text-align: center;
    border-bottom: 1px solid var(--themecolor1);
    margin-bottom: 1rem;
}
.dev-container form {
    display: flex;
    flex-direction: column;
    padding: 1rem;
    background: var(--themecolor1);
    box-shadow: 0 0 5px rgb(33,33,33);
}
.dev-container form label {
    color: White;
    margin-bottom: 5px;
}
.dev-container form input {
    padding: 0.4rem 0.5rem;
    background: var(--color1);
    border: none;
    color: black;
}
.log-container {
    margin-top: 1rem;
    background: var(--themecolor1);
    box-shadow: 0 0 5px rgb(33,33,33);
    color: white;
    max-height: 20rem;
    overflow-y: overlay;
    scrollbar-width: 0;
}
.log-container h2 {
    text-align: center;
    padding: 1rem;
}
.log-container::-webkit-scrollbar { /* WebKit */
    width: 8px;
}
.log-item {
    padding: 0.5rem 1rem;
    display: flex;
    justify-content: space-between;
    align-items: center;
    height: 2.5rem;
}
::-webkit-scrollbar-thumb {
    background: var(--red);
}
.log-item:nth-child(even) {
    background: var(--themecolor2);
}
.log-delete {
    padding: 0.1rem 0.5rem;
    border: none;
    background: var(--red);
    color: white;
    transition: visibility 0.3s, opacity 0.3s, background-color 0.3s ease-in;
    visibility: hidden;
    opacity: 0;
}
.log-visible {
    visibility: visible;
    opacity: 1;
    transition: visibility 0.3s, opacity 0.3s ease-in;
}
.log-loader {
    height: 5rem;
    display: flex;
    justify-content: space-around;
    padding: 0 8rem;
    align-items: center;
}
.log-loader img {
    max-height: 70%;
    animation: spin 1s linear 0s infinite normal none;
}