.auth-form {
    width: 20rem;
    margin: 1rem auto 0 auto;
    padding: 8px 16px 16px;
    background: var(--themecolor1);
    color: white;
    box-shadow: 0 0 8px rgb(15,25,35);
    animation: pop 0.4s ease-out 0s 1 normal none;
    position: relative;
}
@keyframes pop {
    0% {transform: translateY(100vh); opacity: 0;}
    100% {transform: translateY(0vh); opacity: 1}
}
.form-authen {
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    background: rgba(0,0,0,0.5);
}
.form-authen img {
    max-width: 7rem;
    position: absolute;
    margin: auto;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    animation: spin 0.5s linear 0s infinite normal none;
}
.signup-form {
    width: 40rem;
}
.auth-form h2 {
    padding-bottom: 8px;
    border-bottom: 1px solid var(--red)
}
.form-content {
    display: flex;
    justify-content: space-evenly;
    align-items: flex-start;
}
.form-left {
    padding-right: 16px;
    border-right: 1px solid var(--themecolor2);
    width: 50%;
}
.form-right {
    padding-left: 16px;
    width: 50%;
}
.radio-group {
    height: 2.5rem;
    background: white;
    display: flex;
}
.form-radio {
    width: 50%;
    border: none;
    border-radius: 0;
    background: var(--themecolor2);
    color: var(--color1);
    font-size: 1rem;
    transition-duration: 0.5s;
}
.radio-active {
    background: var(--red);
}
.form-submit-button {
    width: 100%;
    margin-top: 1rem;
    padding: 8px;
    background: var(--red);
    border: none;
    color: var(--color1);
    font-size: 1.2rem;
    
}
.auth-form button:focus {
    outline: none
}
.auth-form input {
    height: 2.5rem;
    padding: 0 5px;
    font-size: 1rem;
}

.auth-form-group {
    margin-top: 4px;
}
.form-control {
    display: block;
    width: 100%;
    padding: 4px 0;
}
.form-control:focus {
    outline: none
}
.form-error {
    color: rgb(201, 3, 3);
    font-style: italic;
    font-size: 0.75rem;
    height: 1rem;
}
.form-switch {
    text-align: right;
    margin-top: 0.5rem;
    text-decoration: underline;
    font-style: italic;
    font-size: 0.8rem;
    cursor: pointer;
}